//vars

$cust-violet : #625197;
$cust-rouge : #e21c3e;
$cust-rose : #cc4468;

.section{

  box-shadow: 0px 0px 15px 0px rgba(0,0,0,0.8);

  &:first-of-type{
    color: white;

    background-image: url("./../medias/img/PANA6273.JPG");
    background-position: center;
    background-size: cover;
    font-family: Charter;

    .jumbotron{
      background: linear-gradient(90deg, $cust-rouge 33%, rgba(0, 0, 0, 0) 33%), linear-gradient(90deg, $cust-violet 66%, $cust-rose 66%);
      box-shadow: 0px 0px 15px 0px rgba(0,0,0,0.8);
      border-radius: 0px;
    }

  }

  &:nth-of-type(2){
    background-image: url("./../medias/img/PANA6273.JPG");
    background-position: center;
    background-size: cover;
    // background-color: $cust-rouge;
  }

  &:nth-of-type(3){
    background-image: url("./../medias/img/PANA6273.JPG");
    background-position: center;
    background-size: cover;
    // background-color: $cust-violet;
  }

  &:nth-of-type(4){
    background-image: url("./../medias/img/PANA6273.JPG");
    background-position: center;
    background-size: cover;
    // background-color: $cust-rose;
  }

  .jumbotron {

    .row>.col{

      &:nth-of-type(1)>h3{
        text-decoration: underline;
        text-decoration-color: $cust-rouge;
      }
      &:nth-of-type(2)>h3{
        text-decoration: underline;
        text-decoration-color: $cust-violet;
      }
      &:nth-of-type(3)>h3{
        text-decoration: underline;
        text-decoration-color: $cust-rose;
      }

    }

  }

}

// Fonts
@font-face {
    font-family: Charis;
    src: url("./../fonts/charissil-b.ttf");
}
@font-face {
    font-family: Charter;
    src: url("./../fonts/charter_bold-webfont.ttf");
}
